  view(data) {
    if (data) {
      const ids = [];
      if (data) {
        ids.push(data.invoice_id);
      }
      let dataView = this.vatService.taodulieuXemMau(ids);
      dataView['configs'] = sEnum.cauhinhxemmau.hoadon;
      this.vPreviewInvService.xemMau(dataView);
    } else {
      Library.notify(this._translate.NOTIFY.no_record_selected, 'error');
    }
  }

