import { Component, Input, OnInit } from '@angular/core';
import { AuthService, VStringService, SocketService, Library } from '@core/index';
import { ReportService } from '@modules/report/shared';

import { NgxSpinnerService } from 'ngx-spinner';
import { Subscription } from 'rxjs/Subscription';

@Component({
  selector: 'app-report-use-receipt',
  templateUrl: './report-use-receipt.component.html',
  styleUrls: ['./report-use-receipt.component.scss']
})
export class ReportUseReceiptComponent implements OnInit {
  @Input() findData: any;


  configs: any;
  ioSignature: Subscription;
  dataSource: any = {
    store: []
  };
  invoiceTypeNames: any;
  _translate: any;

  constructor(
    private authService: AuthService,
    private spinner: NgxSpinnerService,
    private socketService: SocketService,
    private reportService: ReportService,
  ) { 
    this._translate = this.authService.getTranslate();
  }

  ngOnInit() {
    this.configs = this.authService.getConfigs();
    const currentUser = this.authService.getCurrentUser();
    const mst = VStringService._getValueToString(currentUser.account.tax_code);
    this.loadList(null);
  }
  loadList(findByData) {
    Library.showloading();
    var data: any = {
      'report': {
        'report_period': this.findData.reportPeriod,
        'from_date': this.findData.from_date,
        'to_date': this.findData.to_date,
        'is_view': true,
        'template_id': null
      }
    };
    if (findByData) {
      data = {
        'report': {
          "from_date": findByData.from_date,
          "to_date": findByData.to_date,
          "is_view": true,
          "template_id": null
        }
      }
    }
    this.dataSource = {
      id: 'order',
      store: []
    };
    this.invoiceTypeNames = this.findData.invoiceTypeNames;
    this.dataSource.store = this.reportService.getAlluseReceipt('/receipts/report-status-use', data);
    Library.hideloading();
  }

}
