import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { IndexComponent } from './components/index/index.component';
import { ReportResolver } from './shared';

const custom_template_brec = [
  {
    label: {brec: "Báo cáo tổng hợp"},
    url: ""
  }
];

const routes: Routes = [
  { path: '', component: IndexComponent, resolve: {configs: ReportResolver}},
  { path: ':type', component: IndexComponent, data: {
    custom_brec: custom_template_brec
  }, resolve: {configs: ReportResolver}}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class routing {}
