import {Component, Input, OnInit, ViewChild} from '@angular/core';
import {BsModalService} from 'ngx-bootstrap/modal';
import {AuthService, Helper, Library} from '@core/index';
import {Report, ReportService} from '@modules/report/shared';
import {DxDataGridComponent} from 'devextreme-angular';
import {TranslateService} from '@ngx-translate/core';
import {sEnum} from '@config/sEnum';
@Component({
  selector: 'app-report-sse',
  templateUrl: './report-sse.component.html',
  styleUrls: ['./report-sse.component.scss']
})
export class ReportSseComponent implements OnInit {
  @ViewChild(DxDataGridComponent) dataGrid: DxDataGridComponent;
  @Input() findData: any;
  @Input() templates: any;
  typeCode: any;
  reportPeriod: any;
  from_date: any;
  to_date: any;
  dataSource: any = {};
  frmData = new Report();
  contextMenu = [{text: 'Thêm'}];
  invoiceTypeNames: any;
  itemSystemExport: any = [];
  _language: string;
  _translate: any;
  template: any;
  isHoaDonKinhDoanhXe = false;
  isVeMayBayTongHop = false;
  isTypeAccount: string;

  constructor(
    private modalService: BsModalService,
    private reportService: ReportService,
    public translate: TranslateService,
    private authService: AuthService,
  ) {
    this._language = this.authService.getLanguage();
    this._translate = this.authService.getTranslate();
    translate.use(this._language);
    this.isTypeAccount = this.authService.getAccountType();
  }

  ngOnInit() {
    this.loadList(null);
  }

  loadList(findByData) {
    Library.showloading();
    let data: any;
      data = {
        'report': {
          'from_date': this.findData.from_date,
          'to_date': this.findData.to_date,
          'report_period': this.findData.reportPeriod,
        }
    }
    if (findByData) {
        data = {
          'report': {
            'from_date': findByData.from_date,
            'to_date': findByData.to_date,
            'report_period': this.findData.reportPeriod,
          }
      }
    }
    this.dataSource = {};
    this.invoiceTypeNames = this.findData.invoiceTypeNames;
    this.dataSource.store = this.reportService.getAll(data, 'usage-invoice');
    Library.hideloading();
  }
  viewUsedNumber(){

  }
  viewDeleteNumber(){
    
  }
}
