import { Component, Input, OnInit, ViewChild } from '@angular/core';
import { BsModalService } from 'ngx-bootstrap/modal';
import { AuthService, Helper, Library, SocketService, VPreviewInvService } from '@core/index';
import { Report, ReportService } from '@modules/report/shared';
import { DxDataGridComponent } from 'devextreme-angular';
import { TranslateService } from '@ngx-translate/core';
import { sEnum } from '@config/sEnum';
import { VatService } from '@modules/vat/shared';
import { NgxSpinnerService } from 'ngx-spinner';
import { Subscription } from 'rxjs/Subscription';
import { ViewInvoiceService } from '@shared/service/view-invoice.service';

@Component({
  selector: 'app-report-list-invoice',
  templateUrl: './report-list-invoice.component.html',
  styleUrls: ['./report-list-invoice.component.scss']
})
export class ReportListInvoiceComponent implements OnInit {
  @ViewChild(DxDataGridComponent) dataGrid: DxDataGridComponent;
  @Input() findData: any;
  @Input() templates: any;
  typeCode: any;
  reportPeriod: any;
  from_date: any;
  to_date: any;
  dataSource: any = {};
  frmData = new Report();
  contextMenu = [{ text: 'Thêm' }];
  invoiceTypeNames: any;
  itemSystemExport: any = [];
  _language: string;
  _translate: any;
  template: any;
  isHoaDonKinhDoanhXe = false;
  isVeMayBayTongHop = false;
  isTypeAccount: string;
  ioSignature: Subscription;
  configs: any = {};
  constructor(
    private reportService: ReportService,
    public translate: TranslateService,
    private authService: AuthService,
    private vatService: VatService,
    private vPreviewInvService : ViewInvoiceService
  ) {
    this._language = this.authService.getLanguage();
    this._translate = this.authService.getTranslate();
    translate.use(this._language);
    this.isTypeAccount = this.authService.getAccountType();
  }

  ngOnInit() {
    this.configs = this.authService.getConfigs();
    this.loadList(null);
  }

  loadList(findByData) {
    Library.showloading();
    let template_id = this.findData.template_id;
    let data: any;
    if (this.isTypeAccount === 'HS-SV') {
      data = {
        'report': {
          'report_period': this.findData.reportPeriod,
          'from_date': this.findData.from_date,
          'to_date': this.findData.to_date,
          'template_id': this.findData.template_id,
          'user_ids': this.findData.user_id,
          'class_codes': this.findData.class_codes,
          'group_type_invoice': this.findData.group_type_invoice
        }
      };
    } else {
      data = {
        'report': {
          'report_period': this.findData.reportPeriod,
          'from_date': this.findData.from_date,
          'to_date': this.findData.to_date,
          'template_id': this.findData.template_id,
          'user_ids': this.findData.user_id,
          'group_type_invoice': this.findData.group_type_invoice
        }
      };
    }
    if (findByData) {
      if (this.isTypeAccount === 'HS-SV') {
        data = {
          'report': {
            'report_period': findByData.reportPeriod,
            'from_date': findByData.from_date,
            'to_date': findByData.to_date,
            'template_id': findByData.template_id,
            'user_ids': findByData.user_id,
            'class_codes': findByData.class_codes,
            'group_type_invoice': findByData.group_type_invoice
          }
        };
      } else {
        data = {
          'report': {
            'report_period': findByData.reportPeriod,
            'from_date': findByData.from_date,
            'to_date': findByData.to_date,
            'template_id': findByData.template_id,
            'user_ids': findByData.user_id,
            'group_type_invoice': findByData.group_type_invoice
          }
        };
      }
      template_id = findByData.template_id;
    }
    if (this.templates && template_id !== '') {
      this.template = this.templates.filter(p => p.template_id === template_id)[0];
      if (this.template) {
        this.isHoaDonKinhDoanhXe = this.template.template_type === sEnum.templatetype.HoaDonKinhDoanhXe;
        this.isVeMayBayTongHop = this.template.template_type === sEnum.templatetype.HoaDonVeMayBayTongHop;
      }
    }
    this.dataSource = {};
    this.invoiceTypeNames = this.findData.invoiceTypeNames;
    this.dataSource.store = this.reportService.getAll(data, 'sold');
    Library.hideloading();
  }

  contextTableClick(e) {
    if (e.itemData.text === 'Thêm') {
      this.addRow();
    }
  }
  view(data) {
    if (data) {
      const ids = [];
      if (data) {
        ids.push(data.id);
      }
      let dataView = this.vatService.taodulieuXemMau(ids);
      dataView['configs'] = sEnum.cauhinhxemmau.hoadon;
      this.vPreviewInvService.xemMau(dataView);
    } else {
      Library.notify(this._translate.NOTIFY.no_record_selected, 'error');
    }
  }
  deleteRow(item) {
    setTimeout(() => {
      const check = confirm('Bạn có đồng ý xóa không?');
      if (check) {
        this.dataSource.remove(item.order);
        this.dataGrid.instance.refresh();
      }
    }, 100);
  }

  addRow() {
    const data = this.frmData.addRow();
    this.dataSource.push(data);
    this.dataGrid.instance.refresh();
  }

  onEditorPreparing(e: any) {

    if (e.dataField !== 'invoice_type') {
      if (e.row.data.invoice_type === '') {
        e.cancel = true;
        Library.notify('Bạn chưa chọn loại hóa đơn', 'error');
      }
    }

    if (e.row.data) {
      e.editorOptions.disabled = e.row.data.system_export === '1' || e.dataField === 'amount' || e.dataField === 'closing_amount';
    }
    if (e.dataField === 'invoice_series') {
    }


  }

  onCellPrepared(e: any) {
    if (e.columnIndex === 0 && e.rowType === 'data') {
      if (this.itemSystemExport.includes(e.rowIndex)) {
        e.cellElement.querySelector('.dx-link-delete').remove();
      }

    }
  }

  onRowPrepared(e: any) {
    if (e.data) {
      if (e.data.system_export === '1') {
        e.rowElement.style.background = '#F1F1F1';
        this.itemSystemExport.push(e.rowIndex);
      }
    }
  }


  onRowUpdated(e) {
    this.invoiceTypeNames.forEach(invoiceType => {
      if (invoiceType.code === e.data.invoice_type) {
        e.key.invoice_type_name = invoiceType.name;
      }
    });
    const column = Object.keys(e.data)[0];
    switch (column) {
      case 'opening_begin_number':
        if (e.key.opening_begin_number !== '') {
          e.key.opening_begin_number = Helper.formatNumberInvoice(e.key.opening_begin_number);
        }

        if (e.key.opening_end_number !== '' || e.key.opening_begin_number !== '') {
          e.key.amount = e.key.opening_end_number - e.key.opening_begin_number + 1;
        } else {
          e.key.amount = '';
        }

        if (e.key.in_period_release_end_number || e.key.in_period_release_begin_number) {
          e.key.amount += e.key.in_period_release_end_number - e.key.in_period_release_begin_number + 1;
        }
        this.dataGrid.instance.refresh();
        break;
      case 'opening_end_number':
        if (e.key.opening_end_number !== '') {
          e.key.opening_end_number = Helper.formatNumberInvoice(e.key.opening_end_number);
        }

        if (e.key.opening_end_number !== '' || e.key.opening_begin_number !== '') {
          e.key.amount = e.key.opening_end_number - e.key.opening_begin_number + 1;
        } else {
          e.key.amount = '';
        }

        if (e.key.in_period_release_end_number || e.key.in_period_release_begin_number) {
          e.key.amount += e.key.in_period_release_end_number - e.key.in_period_release_begin_number + 1;
        }
        this.dataGrid.instance.refresh();
        break;
      case 'in_period_release_begin_number':
        if (e.key.in_period_release_begin_number !== '') {
          e.key.in_period_release_begin_number = Helper.formatNumberInvoice(e.key.in_period_release_begin_number);
        }

        if (e.key.in_period_release_end_number !== '' || e.key.in_period_release_begin_number !== '') {
          e.key.amount = e.key.in_period_release_end_number - e.key.in_period_release_begin_number + 1;
        } else {
          e.key.amount = '';
        }

        if (e.key.opening_end_number || e.key.opening_begin_number) {
          e.key.amount += e.key.opening_end_number - e.key.opening_begin_number + 1;
        }
        this.dataGrid.instance.refresh();

        break;
      case 'in_period_release_end_number':
        if (e.key.in_period_release_end_number !== '') {
          e.key.in_period_release_end_number = Helper.formatNumberInvoice(e.key.in_period_release_end_number);
        }

        if (e.key.in_period_release_end_number !== '' || e.key.in_period_release_begin_number !== '') {
          e.key.amount = e.key.in_period_release_end_number - e.key.in_period_release_begin_number + 1;
        } else {
          e.key.amount = '';
        }

        if (e.key.opening_end_number || e.key.opening_begin_number) {
          e.key.amount += e.key.opening_end_number - e.key.opening_begin_number + 1;
        }
        this.dataGrid.instance.refresh();
        break;
      case 'closing_begin_number':
        if (e.key.closing_begin_number !== '') {
          e.key.closing_begin_number = Helper.formatNumberInvoice(e.key.closing_begin_number);
        }

        if (e.key.closing_end_number !== '' || e.key.closing_begin_number !== '') {
          e.key.closing_amount = e.key.closing_end_number - e.key.closing_begin_number + 1;
        } else {
          e.key.closing_amount = '';
        }

        this.dataGrid.instance.refresh();
        break;
      case 'closing_end_number':
        if (e.key.closing_end_number !== '') {
          e.key.closing_end_number = Helper.formatNumberInvoice(e.key.closing_end_number);
        }

        if (e.key.closing_end_number !== '' || e.key.closing_begin_number !== '') {
          e.key.closing_amount = e.key.closing_end_number - e.key.closing_begin_number + 1;
        } else {
          e.key.closing_amount = '';
        }

        this.dataGrid.instance.refresh();
        break;
      case 'in_period_used_begin_number':
        if (e.key.in_period_used_begin_number !== '') {
          e.key.in_period_used_begin_number = Helper.formatNumberInvoice(e.key.in_period_used_begin_number);
        }
        this.dataGrid.instance.refresh();
        break;
      case 'in_period_used_end_number':
        if (e.key.in_period_used_end_number !== '') {
          e.key.in_period_used_end_number = Helper.formatNumberInvoice(e.key.in_period_used_end_number);
        }
        this.dataGrid.instance.refresh();
        break;
      case 'invoice_series':
        if (e.key.invoice_series !== '') {
          e.key.invoice_series = e.key.invoice_series.toUpperCase();
        }
        this.dataGrid.instance.refresh();
        break;
      case 'template_code':
        if (e.key.template_code !== '') {
          e.key.template_code = e.key.template_code.toUpperCase();
        }
        this.dataGrid.instance.refresh();
        break;

      default:
        break;
    }
  }

  onContentReady(e) {
    e.component.columnOption('command:edit', {
      visibleIndex: -1
    });
  }


  checkStartEnd(start, end) {
    if (start > end) {
      return false;
    }
    return true;
  }

  customizeText(e) {
    if (!e.value) {
      e.value = 0;
    }
    return e.value;
  }
  
  ngOnDestroy() {
  }
}
