import { Component, OnInit, ViewChild } from '@angular/core';

import { BsModalService } from 'ngx-bootstrap/modal';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';

import { Library, Helper } from '@core/index';
import { ReportService, Adress, Report } from '@modules/report/shared';
import { DxDataGridComponent, DxFormComponent } from 'devextreme-angular';

@Component({
  selector: 'app-export-xml',
  templateUrl: './export-xml.component.html',
  styleUrls: ['./export-xml.component.scss']
})
export class ExportXmlComponent implements OnInit {
  @ViewChild(DxDataGridComponent) dataGrid: DxDataGridComponent;

  adress = new Adress();
  name: any;
  tax_code: any;
  typeCode: any;
  info_export_declaration: any;
  citis: any;
  quanhuyen: any;
  phuongxa: any;
  isDisQuanHuyen = true;
  isDisPhuongXa = true;
  isDisSoNha = true;

  reportPeriod: any;
  from_date: any;
  to_date: any;
  province_name: any;
  district_name: any;
  commune_name: any;

  dataSource: any = {};

  frmData = new Report();
  contextMenu = [{ text: 'Thêm' }];
  invoiceTypeNames: any;
  itemSystemExport: any = [];
  needSummaryUpdate: boolean = false;
  total_invoive_temp:any;
  constructor(
    public bsModalRef: BsModalRef,
    private modalService: BsModalService,
    private reportService: ReportService,
  ) { }

  ngOnInit() {
    this.loadList();
    if (this.info_export_declaration.province_code) {
      this.adress.province_code = this.info_export_declaration.province_code;
      this.getQuanHuyen(this.info_export_declaration.province_code);
    }

    if (this.info_export_declaration.province_code) {
      this.adress.district_code = this.info_export_declaration.district_code;
      this.getXaPhuong(this.info_export_declaration.district_code);

    }

    if (this.info_export_declaration.commune_code) {
      this.adress.commune_code = this.info_export_declaration.commune_code;

    }

    if (this.info_export_declaration.home_address) {
      this.adress.home_address = this.info_export_declaration.home_address;
    }

    this.invoiceTypeNames.shift();
  }

  loadList() {
    let data = {
      "report": {
        "report_period": this.reportPeriod,
        "from_date": this.from_date,
        "to_date": this.to_date,
      }
    }
    //this.dataSource.store = this.reportService.getAll(data, this.typeCode);
    this.reportService.getAll1(data, this.typeCode).subscribe((res: any) => {
      this.dataSource = res.data.report.items;
      this.total_invoive_temp = res.data.report.total_invoive_temp;
      let tempRow = this.frmData.tempRow();
      tempRow.forEach(element => {
        this.dataSource.push(element);
      });
    });
  }

  onCitis = (e) => {
    this.province_name = e.component.option("selectedItem").name;
    this.isDisQuanHuyen = false;
    this.getQuanHuyen(e.value);
  }

  getQuanHuyen(value) {
    let data = {
      "list": {
        "listtype_code": "DM_QUAN_HUYEN_CQHC",
        "tag_name": "tinh_thanh",
        "tag_value": value,
      }
    }
    this.reportService.getOrtherList(data).subscribe((res: any) => {
      this.quanhuyen = res.data.lists;
    })
  }

  onQuanHuyen = (e) => {
    this.district_name = e.component.option("selectedItem").name;
    this.isDisPhuongXa = false;
    this.getXaPhuong(e.value);
  }

  getXaPhuong(value) {
    let data = {
      "list": {
        "listtype_code": "DM_XA_PHUONG_CQHC",
        "tag_name": "quan_huyen",
        "tag_value": value,
      }
    }
    this.reportService.getOrtherList(data).subscribe((res: any) => {
      this.phuongxa = res.data.lists;
    })
  }

  onXaPhuong = (e) => {
    this.commune_name = e.component.option("selectedItem").name;
    this.isDisSoNha = false;
  }

  contextTableClick(e) {
    if (e.itemData.text == 'Thêm') {
      this.addRow();
    }
  }
  deleteRow(item) {
    setTimeout(() => {
      let check = confirm('Bạn có đồng ý xóa không?');
      if (check) {
        this.dataSource.remove(item.order);
        this.dataGrid.instance.refresh();
      }
    }, 100);
  }

  addRow() {
    let data = this.frmData.addRow();
    this.dataSource.push(data);
    this.dataGrid.instance.refresh();
  }

  onEditorPreparing(e: any) {

    if (e.dataField != "invoice_type") {
      if (e.row.data.invoice_type == "") {
        e.cancel = true;
        Library.notify("Bạn chưa chọn loại hóa đơn", "error");
      }
    }

    if (e.row.data) {
      e.editorOptions.disabled = e.row.data.system_export == "1";
    }
    if (e.dataField == "invoice_series") {
      // e.editorOptions.onKeyDown = (ekey) => {
      // }
    }


  }
  onCellPrepared(e: any) {
    if (e.columnIndex == 0 && e.rowType == "data") {
      if (this.itemSystemExport.includes(e.rowIndex)) {
        e.cellElement.querySelector(".dx-link-delete").remove();
      }

    }
  }

  onRowPrepared(e: any) {
    if (e.data) {
      if (e.data.system_export == "1") {
        e.rowElement.style.background = "#F1F1F1";
        this.itemSystemExport.push(e.rowIndex);
      }
    }
  }


  onRowUpdated(e) {
    this.invoiceTypeNames.forEach(invoiceType => {
      if (invoiceType.code == e.data.invoice_type) {
        e.key.invoice_type_name = invoiceType.name;
      }
    });
    let column = Object.keys(e.data)[0];
    switch (column) {
      case "opening_begin_number":
        if (e.key.opening_begin_number != "") {
          e.key.opening_begin_number = Helper.formatNumberInvoice(e.key.opening_begin_number);
        }

        if (e.key.opening_end_number != "" || e.key.opening_begin_number != "") {
          e.key.amount = e.key.opening_end_number - e.key.opening_begin_number + 1;
        } else {
          e.key.amount = "";
        }

        if (e.key.in_period_release_end_number || e.key.in_period_release_begin_number) {
          e.key.amount += e.key.in_period_release_end_number - e.key.in_period_release_begin_number + 1;
        }
        this.dataGrid.instance.refresh();
        break;
      case "opening_end_number":
        if (e.key.opening_end_number != "") {
          e.key.opening_end_number = Helper.formatNumberInvoice(e.key.opening_end_number);
        }

        if (e.key.opening_end_number != "" || e.key.opening_begin_number != "") {
          e.key.amount = e.key.opening_end_number - e.key.opening_begin_number + 1;
        } else {
          e.key.amount = "";
        }

        if (e.key.in_period_release_end_number || e.key.in_period_release_begin_number) {
          e.key.amount += e.key.in_period_release_end_number - e.key.in_period_release_begin_number + 1;
        }
        this.dataGrid.instance.refresh();
        break;
      case "in_period_release_begin_number":
        if (e.key.in_period_release_begin_number != "") {
          e.key.in_period_release_begin_number = Helper.formatNumberInvoice(e.key.in_period_release_begin_number);
        }

        if (e.key.in_period_release_end_number != "" || e.key.in_period_release_begin_number != "") {
          e.key.amount = e.key.in_period_release_end_number - e.key.in_period_release_begin_number + 1;
        } else {
          e.key.amount = "";
        }

        if (e.key.opening_end_number || e.key.opening_begin_number) {
          e.key.amount += e.key.opening_end_number - e.key.opening_begin_number + 1;
        }
        this.dataGrid.instance.refresh();

        break;
      case "in_period_release_end_number":
        if (e.key.in_period_release_end_number != "") {
          e.key.in_period_release_end_number = Helper.formatNumberInvoice(e.key.in_period_release_end_number);
        }

        if (e.key.in_period_release_end_number != "" || e.key.in_period_release_begin_number != "") {
          e.key.amount = e.key.in_period_release_end_number - e.key.in_period_release_begin_number + 1;
        } else {
          e.key.amount = "";
        }

        if (e.key.opening_end_number || e.key.opening_begin_number) {
          e.key.amount += e.key.opening_end_number - e.key.opening_begin_number + 1;
        }
        this.dataGrid.instance.refresh();
        break;
      case "closing_begin_number":
        if (e.key.closing_begin_number != "") {
          e.key.closing_begin_number = Helper.formatNumberInvoice(e.key.closing_begin_number);
        }

        if (e.key.closing_end_number != "" || e.key.closing_begin_number != "") {
          e.key.closing_amount = e.key.closing_end_number - e.key.closing_begin_number + 1;
        } else {
          e.key.closing_amount = "";
        }

        this.dataGrid.instance.refresh();
        break;
      case "closing_end_number":
        if (e.key.closing_end_number != "") {
          e.key.closing_end_number = Helper.formatNumberInvoice(e.key.closing_end_number);
        }

        if (e.key.closing_end_number != "" || e.key.closing_begin_number != "") {
          e.key.closing_amount = e.key.closing_end_number - e.key.closing_begin_number + 1;
        } else {
          e.key.closing_amount = "";
        }

        this.dataGrid.instance.refresh();
        break;
      case "in_period_used_begin_number":
        if (e.key.in_period_used_begin_number != "") {
          e.key.in_period_used_begin_number = Helper.formatNumberInvoice(e.key.in_period_used_begin_number);
        }
        this.dataGrid.instance.refresh();
        break;
      case "in_period_used_end_number":
        if (e.key.in_period_used_end_number != "") {
          e.key.in_period_used_end_number = Helper.formatNumberInvoice(e.key.in_period_used_end_number);
        }
        this.dataGrid.instance.refresh();
        break;
      case "invoice_series":
        if (e.key.invoice_series != "") {
          e.key.invoice_series = e.key.invoice_series.toUpperCase();
        }
        this.dataGrid.instance.refresh();
        break;
      case "template_code":
        if (e.key.template_code != "") {
          e.key.template_code = e.key.template_code.toUpperCase();
        }
        this.dataGrid.instance.refresh();
        break;

      default:
        break;
    }
  }

  onContentReady(e) {
    e.component.columnOption("command:edit", {
      visibleIndex: -1
    });
  }


  checkStartEnd(start, end) {
    if (start > end) {
      return false;
    }
    return true;
  }
  customizeText(e) {
    if (!e.value) {
      e.value = 0;
    }
    return e.value;
  }

  onSubmit = (e) => {
    let items = [];
    this.dataSource.forEach(item => {
      if (item.invoice_type != "") {
        items.push(item);
      }
    });
    if (!this.province_name) {
      if(this.citis)
      {
        this.citis.forEach(city => {
          if (city.code == this.adress.province_code) {
            this.province_name = city.name
          }
        });
      }
    }
    if (!this.district_name) {
      if(this.quanhuyen)
      {
        this.quanhuyen.forEach(qh => {
          if (qh.code == this.adress.district_code) {
            this.district_name = qh.name
          }
        });
      }
    }
    if (!this.commune_name) {
      if(this.phuongxa)
      {
        this.phuongxa.forEach(px => {
          if (px.code == this.adress.commune_code) {
            this.commune_name = px.name
          }
        });
      }
    }
    if (!this.province_name) {
      Library.notify("Tỉnh/Thành phố không được để trống", "error");
      return;
    }
    if (!this.district_name) {
      Library.notify("Quận/Huyện không được để trống", "error");
      return;
    }
    if (!this.commune_name) {
      Library.notify("Phường/Xã không được để trống", "error");
      return;
    }
    let totalAmount = this.dataGrid.instance.getTotalSummaryValue("totalAmount");
    let totalInPeriodUsedTotal = this.dataGrid.instance.getTotalSummaryValue("totalInPeriodUsedTotal");
    let totalClosingAmount = this.dataGrid.instance.getTotalSummaryValue("totalClosingAmount");
    let ClosingAmount = this.dataGrid.instance.getTotalSummaryValue("closing_amount");


    let data = {
      "report": {
        "report_period": this.reportPeriod,
        "closing_amount": ClosingAmount,
        "from_date": this.from_date,
        "to_date": this.to_date,
        "province_code": this.adress.province_code,
        "province_name": this.province_name,
        "district_code": this.adress.district_code,
        "district_name": this.district_name,
        "commune_code": this.adress.commune_code,
        "commune_name": this.commune_name,
        "home_address": this.adress.home_address,
        "total_amount": totalAmount,
        "total_in_period_used_total": totalInPeriodUsedTotal,
        "total_closing_amount": totalClosingAmount,
        "items": items,
        "total_invoive_temp": this.total_invoive_temp
      }
    }
    if (this.typeCode) {
      Library.showloading();
      this.reportService.exportXml(data, this.typeCode).subscribe((res: any) => {
        if (res.status == 'success') {
          let file = res.data.report;
          Helper.dowloadFile(file.file_name, 'xlsx', file.file_content);
          Library.notify("Xuất file XML thành công", 'success');
          this.bsModalRef.hide();
        } else {
          Library.notify(res.message, 'error');
        }
        Library.hideloading();
      })
    } else {
      Library.notify("Bạn phải chọn loại báo cáo", "error");
    }

  }


}
